// helpers/alertService.js
// Servicio para manejar todas las alertas con SweetAlert2

import Swal from 'sweetalert2';

/**
 * Configuraci√≥n base para las alertas
 */
const alertConfig = {
  confirmButtonColor: '#ff6b35',
  background: '#fff',
  color: '#333'
};

/**
 * Muestra una alerta de campo vac√≠o
 */
export const showEmptyFieldAlert = () => {
  return Swal.fire({
    icon: 'warning',
    title: '¬°Campo vac√≠o!',
    text: 'Por favor, ingresa un nombre o ID de Pok√©mon',
    confirmButtonText: 'Entendido',
    ...alertConfig
  });
};

/**
 * Muestra una alerta de fuera de rango
 */
export const showOutOfRangeAlert = () => {
  return Swal.fire({
    icon: 'error',
    title: '¬°Fuera de rango!',
    html: `
      <p>ID de Pok√©mon fuera de rango v√°lido.</p>
      <br>
      <strong>üéØ Rango permitido:</strong> 1 - 1025
      <br><br>
      <em>üí° Tambi√©n puedes usar el nombre del Pok√©mon</em>
    `,
    confirmButtonText: 'Intentar de nuevo',
    confirmButtonColor: '#e74c3c',
    background: '#fff',
    color: '#333'
  });
};

/**
 * Muestra una alerta de √©xito cuando se encuentra un Pok√©mon
 * @param {string} pokemonName - Nombre del Pok√©mon encontrado
 */
export const showPokemonFoundAlert = (pokemonName) => {
  return Swal.fire({
    icon: 'success',
    title: '¬°Pok√©mon encontrado!',
    text: `${pokemonName} cargado exitosamente`,
    timer: 2000,
    timerProgressBar: true,
    showConfirmButton: false,
    toast: true,
    position: 'top-end',
    background: '#d4edda',
    color: '#155724'
  });
};

/**
 * Muestra una alerta de Pok√©mon no encontrado
 * @param {string} pokemonInput - El input que no se encontr√≥
 */
export const showPokemonNotFoundAlert = (pokemonInput) => {
  return Swal.fire({
    icon: 'error',
    title: '¬°Pok√©mon no encontrado!',
    html: `
      <p><strong>"${pokemonInput}"</strong> no existe en la Pok√©dex.</p>
      <br>
      <div style="text-align: left; margin: 0 auto; display: inline-block;">
        <strong>üí° Sugerencias:</strong><br>
        ‚Ä¢ Verifica la ortograf√≠a del nombre<br>
        ‚Ä¢ Usa nombres en ingl√©s (ej: pikachu, charizard)<br>
        ‚Ä¢ Prueba con un ID v√°lido (1-1025)<br>
        ‚Ä¢ Revisa que no tenga espacios extra
      </div>
    `,
    confirmButtonText: 'Intentar de nuevo',
    confirmButtonColor: '#e74c3c',
    background: '#fff',
    color: '#333',
    width: '500px'
  });
};

/**
 * Muestra una alerta de error de conexi√≥n
 * @param {string} errorMessage - Mensaje de error
 */
export const showConnectionErrorAlert = (errorMessage) => {
  return Swal.fire({
    icon: 'error',
    title: '¬°Error de conexi√≥n!',
    text: errorMessage,
    confirmButtonText: 'Reintentar',
    confirmButtonColor: '#f39c12',
    background: '#fff',
    color: '#333'
  });
};

/**
 * Muestra la alerta apropiada basada en el tipo de error
 * @param {string} errorType - Tipo de error
 * @param {string} pokemonInput - Input del usuario (opcional)
 * @param {string} errorMessage - Mensaje de error personalizado (opcional)
 */
export const showErrorAlert = (errorType, pokemonInput = '', errorMessage = '') => {
  switch (errorType) {
    case 'EMPTY_FIELD':
      return showEmptyFieldAlert();
    
    case 'OUT_OF_RANGE':
      return showOutOfRangeAlert();
    
    case 'NOT_FOUND':
      return showPokemonNotFoundAlert(pokemonInput);
    
    case 'CONNECTION_ERROR':
      return showConnectionErrorAlert(errorMessage);
    
    default:
      return showConnectionErrorAlert('Error desconocido');
  }
};
