// helpers/pokemonService.js
// Servicio para manejar todas las operaciones relacionadas con la Pok√©API

import { pokeapi } from "@/api/pokeapi";

/**
 * Valida el input del usuario para b√∫squeda de Pok√©mon
 * @param {string} pokemonInput - El input del usuario
 * @returns {Object} - Objeto con isValid, error y pokemonNumber (si aplica)
 */
export const validatePokemonInput = (pokemonInput) => {
  if (!pokemonInput.trim()) {
    return {
      isValid: false,
      error: 'EMPTY_FIELD',
      message: 'Por favor, ingresa un nombre o ID de Pok√©mon'
    };
  }

  const trimmedInput = pokemonInput.trim();
  const isNumeric = /^\d+$/.test(trimmedInput);
  
  if (isNumeric) {
    const pokemonNumber = parseInt(trimmedInput);
    if (pokemonNumber < 1 || pokemonNumber > 1025) {
      return {
        isValid: false,
        error: 'OUT_OF_RANGE',
        message: 'ID de Pok√©mon fuera de rango v√°lido (1-1025)'
      };
    }
    return {
      isValid: true,
      pokemonNumber,
      input: trimmedInput
    };
  }

  return {
    isValid: true,
    input: trimmedInput
  };
};

/**
 * Busca un Pok√©mon en la Pok√©API
 * @param {string} pokemonIdentifier - Nombre o ID del Pok√©mon
 * @returns {Promise<Object>} - Datos del Pok√©mon
 */
export const fetchPokemon = async (pokemonIdentifier) => {
  try {
    const response = await fetch(`${pokeapi}${pokemonIdentifier.toLowerCase()}`);
    
    if (!response.ok) {
      if (response.status === 404) {
        throw new Error(`El Pok√©mon "${pokemonIdentifier}" no existe. Verifica el nombre o ID.`);
      } else {
        throw new Error(`Error de conexi√≥n (${response.status}). Int√©ntalo de nuevo.`);
      }
    }
    
    return await response.json();
  } catch (error) {
    throw error;
  }
};

/**
 * Busca informaci√≥n de la especie del Pok√©mon
 * @param {string} speciesUrl - URL de la especie desde la respuesta del Pok√©mon
 * @returns {Promise<Object>} - Datos de la especie
 */
export const fetchPokemonSpecies = async (speciesUrl) => {
  try {
    const response = await fetch(speciesUrl);
    
    if (!response.ok) {
      throw new Error(`Error al obtener informaci√≥n de la especie (${response.status})`);
    }
    
    return await response.json();
  } catch (error) {
    throw error;
  }
};

/**
 * Funci√≥n principal para buscar un Pok√©mon completo (datos + especie)
 * @param {string} pokemonIdentifier - Nombre o ID del Pok√©mon
 * @returns {Promise<Object>} - Objeto con pokemon y species
 */
export const searchCompletePokemon = async (pokemonIdentifier) => {
  try {
    // Buscar datos b√°sicos del Pok√©mon
    const pokemon = await fetchPokemon(pokemonIdentifier);
    
    // Buscar informaci√≥n de la especie
    const species = await fetchPokemonSpecies(pokemon.species.url);
    
    return {
      pokemon,
      species
    };
  } catch (error) {
    throw error;
  }
};

/**
 * Formatea el nombre del Pok√©mon para mostrar
 * @param {string} name - Nombre del Pok√©mon
 * @returns {string} - Nombre formateado con primera letra may√∫scula
 */
export const formatPokemonName = (name) => {
  return name.charAt(0).toUpperCase() + name.slice(1);
};

/**
 * Convierte la altura del Pok√©mon de dec√≠metros a metros
 * @param {number} height - Altura en dec√≠metros
 * @returns {string} - Altura formateada en metros
 */
export const formatHeight = (height) => {
  return (height / 10).toFixed(1);
};

/**
 * Convierte el peso del Pok√©mon de hectogramos a kilogramos
 * @param {number} weight - Peso en hectogramos
 * @returns {string} - Peso formateado en kilogramos
 */
export const formatWeight = (weight) => {
  return (weight / 10).toFixed(1);
};

/**
 * Formatea el ID del Pok√©mon con ceros a la izquierda
 * @param {number} id - ID del Pok√©mon
 * @returns {string} - ID formateado con 3 d√≠gitos
 */
export const formatPokemonId = (id) => {
  return id.toString().padStart(3, '0');
};
